import Vue from 'vue'
import Router from 'vue-router'
//import Home from '@/components/Home'
import Home_plus_new from '@/components/Home_plus_new'
import 专项概况 from '@/components/专项概况'
import 专项概况_专项简介 from '@/components/专项概况_专项简介'
import 专项概况_研究内容 from '@/components/专项概况_研究内容'
import 专项概况_研究目标 from '@/components/专项概况_研究目标'
import 专项概况_项目设置 from '@/components/专项概况_项目设置'
import 专项动态 from '@/components/专项动态'
import 亮点成果 from '@/components/亮点成果'
import 共享平台 from '@/components/共享平台'
import 合作交流 from '@/components/合作交流'
import 专项管理 from '@/components/专项管理'
import 专项管理_专家详情 from '@/components/专项管理_专家详情'
import 专项管理_详情 from '@/components/专项管理_详情'
import 专项动态_详情 from '@/components/专项动态_详情'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home_plus_new',
      component: Home_plus_new,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/gaikuang',
      name: '专项概况',
      component: 专项概况,
      meta: {
        title: '专项概况'
      },
      // redirect: '/gaikuang/jianjie',
      children: [{
        path: '/gaikuang/jianjie',
        name: '专项概况',
        component: 专项概况,
        },
        {
          path: '/gaikuang/tuandui',
          name: '专项概况',
          component: 专项概况
        },
        {
          path: '/gaikuang/mubiao',
          name: '专项概况',
          component: 专项概况
        },
        {
          path: '/gaikuang/shezhi',
          name: '专项概况',
          component: 专项概况
        },]
    },
    {
      path: '/dongtai',
      name: '专项动态',
      component: 专项动态,
      meta: {
        title: '专项动态'
      },
    },
    {
      path: '/chengguo',
      name: '亮点成果',
      component: 亮点成果,
      meta: {
        title: '亮点成果'
      },
    },
    {
      path: '/pingtai',
      name: '共享平台',
      component: 共享平台,
      meta: {
        title: '共享平台'
      },
    },
    {
      path: '/jiaoliu',
      name: '合作交流',
      component: 合作交流,
      meta: {
        title: '合作交流'
      },
    },
    {
      path: '/guanli',
      name: '专项管理',
      component: 专项管理,
      children: [{
        path: '/guanli/zongti',
        name: '专项管理',
        component: 专项管理,
      },
        {
          path: '/guanli/bangongshi',
          name: '专项管理',
          component: 专项管理
        },
        {
          path: '/guanli/banfa',
          name: '专项管理',
          component: 专项管理
        },
        {
          path: '/guanli/guanyu',
          name: '专项管理',
          component: 专项管理
        },],
      meta: {
        title: '专项管理'
      },
    },
    {
      path: '/guanli/zhuanjia',
      name: '专项管理_专家详情',
      component: 专项管理_专家详情,
      meta: {
        title: '专项管理_专家详情'
      },
    },
    {
      path: '/dongtai/xiangqing',
      name: '专项动态_详情',
      component: 专项动态_详情,
      meta: {
        title: '专项动态_详情'
      },
    },
    {
      path: '/guanli/xiangqing',
      name: '专项管理_详情',
      component: 专项管理_详情,
      meta: {
        title: '专项管理_详情'
      },
    }
  ]
})



