<template>
  <div class="child">
    <div class="header_bottom" style="background-color: #004F9F;">
      <div>
        <el-menu
          :default-active="1" router
          id="header_bottom_menu"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#004F9F"
          text-color="#fff"
          active-text-color="#000"
          style="height:100%;border: 0; background: rgba(15, 38, 69, 0.01);">
          <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
            {{ item.navItem }}
          </el-menu-item>
        </el-menu>
      </div>
      <div class="header_bottom_logo">
        <a href="#"><img :src="img_logo" style="height: 90%" alt=""></a>
      </div>
    </div>

    <div class="content" style="margin-top: 40px">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/dongtai' }">专项动态</el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>

      <div style="min-height: 557px" v-html="text">
        {{text}}
      </div>
    </div>

    <div class="bottom">
      <div style="width: 1200px;margin: 0 auto">
        <div style="float: right;padding-top: 30px">
          <img :src="this.$store.state.img6" style="width: 90px" alt="">
        </div>
        <div style="padding-top: 67px">
          <img :src="this.$store.state.img_bottom_address" style="width: 16px" alt=""> {{this.$store.state.address}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          <img :src="this.$store.state.img_bottom_postcode" style="width: 16px" alt=""> {{this.$store.state.postcode}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          版权所有 {{this.$store.state.copyright}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          备案号：{{this.$store.state.for_the_record}}
        </div>
        <div style="padding-top: 70px;text-align: center;font-size: 12px">
          {{this.$store.state.bottom_text}}
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
          <a href="#" style="color: white">用户留言</a>丨<a href="#" style="color: white">管理员登录</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "专项管理_专家详情",
    data() {
      return {
        navList: [
          {name: '/', navItem: '首页'},
          {name: '/gaikuang', navItem: '专项概况'},
          {name: '/dongtai', navItem: '专项动态'},
          {name: '/chengguo', navItem: '亮点成果'},
          {name: '/pingtai', navItem: '共享平台'},
          {name: '/jiaoliu', navItem: '合作交流'},
          {name: '/guanli', navItem: '专项管理'},
        ],
        text:'<div style="text-align: center;padding: 100px">这是一个div标签</div>',
        img_logo: require('@/assets/img/logo.png'),
        img_专项管理_总体: require('@/assets/img/专项管理_总体.png'),
        img_专项管理_专家详情_专家: require('@/assets/img/专项管理_专家详情_专家.png'),
        backgroundDiv: {
          backgroundImage: 'url(' + require('@/assets/img/专项管理_专家详情_top.png') + ')',
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'auto 100%',
          backgroundPosition:'center center'
        },
      }
    },
    methods:{
      getData(){
        let that=this;
        let id=this.$route.query.id;
        let subject=this.$route.query.subjectType;
      this.axios.post("http://159.226.158.119:8888/web/news/detail",  {"newsId": id, "subject": subject})
        .then(function (res) {
          console.log(1);
          that.text=res.data.data.details
        }.bind(this))
        .catch(function (err) {
          if (err.response) {
            console.log(err.response)
          }
        }.bind(this))
    }
    },
    mounted() {
      this.getData();
    }
  }
</script>

<style scoped>
  #header_bottom_menu {
    width: 60%;
    float: right;
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    line-height: 30px;
  }

  #header_bottom_menu .el-menu-item {
    font-size: 16px;
    height: 79px;
    padding-top: 9px
  }

  #header_bottom_menu .el-menu-item:hover {
    background: #dee7f0 !important;
    color: #000 !important;
  }

  #header_bottom_menu .el-menu-item.is-active {
    background: #dee7f0 !important;
  }

</style>



