          '的基础与应用研究,建设国际领先的数字地球科学平台，推动地球大数据领域的技术创新与地球系统科学的重大突破和科学发现' +
          '，实现全方位宏观决策支持和社会公众知识传播服务，为“一带一路”、“数字中国"、人类命运共同体和联合国可持续发展目标等' +
          '国内外重大战略提供科技支撑和决策支持。',
        backgroundDiv: {
          backgroundImage: 'url(' + require('@/assets/img/合作交流_背景图.png') + ')',
          backgroundRepeat: 'no-repeat',
