    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData(n1, n2) {
        // 这里使用axios，使用时请提前引入
        this.axios.post('http://159.226.158.119:8888/web/news/list', {
            "newsType": 0,
            // 每页显示的条数
            "PageSize": n1,
            // 显示第几页
            "pageNumber": n2,
          }, {emulateJSON: true},
          {
            headers: {"Content-Type": "application/x-www-form-urlencoded;charset=utf-8",}
          }
        ).then(reponse => {
          console.log(reponse);
          // 将数据赋值给tableData
          this.tableData = reponse.data.data.records;
          // 将数据的长度赋值给totalCount
          this.totalCount = reponse.data.data.records.length;
        })
      },
      // 分页
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData(val, 1)
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData(this.PageSize, (val) * (this.pageSize))
      },
      jump_to_xiangqing(){
        window.location.href="/#/dongtai/xiangqing";
      },
      menu_change() {
        /*滚轮事件只有firefox比较特殊，使用DOMMouseScroll; 其他浏览器使用mousewheel;*/
        // firefox
        var direction;
        let menu=document.getElementsByClassName('header_bottom')[0];
        document.body.addEventListener("DOMMouseScroll", function (event) {
          direction = event.detail && (event.detail > 0 ? "mousedown" : "mouseup");
          menu.style.top='-100px';
          if(event.detail<0){
            menu.style.position='fixed';
            menu.style.top='0';
          }else{

          }
        });

        // chrome and ie
        document.body.onmousewheel = function (event) {
          event = event || window.event;
          direction = event.wheelDelta && (event.wheelDelta > 0 ? "mouseup" : "mousedown");
          menu.style.top='-100px';
          if(event.wheelDelta>0){
            menu.style.position='fixed';
            menu.style.top='0';
          }
        };
      },
      get_page_data(){
        this.axios.post("url",  {"newsType": 0, "pageNumber": 1, "pageSize": 10})
          .then(function (res) {
            //成功
          }.bind(this))
          .catch(function (err) {
            if (err.response) {
              console.log(err.response)
            }
          }.bind(this))
      }
    },
    created: function () {
      this.getData(this.PageSize, this.currentPage)
    },
    mounted() {
      this.menu_change();
    },
