        navList_child: [
          {name: '/gaikuang', navItem: '专项简介'},
          {name: '/gaikuang', navItem: '研究内容'},
          {name: '/gaikuang', navItem: '研究目标'},
          {name: '/gaikuang', navItem: '项目设置'},
        ],
        jianjie_text: '印太交汇区是全球物质循环和能量交换最强烈的区域，该海域形成了全球海洋温度最高和面积最大的暖池区，' +
          '为驱动热带大气环流提供了主要的热源，同时，太平洋板块、欧亚板块与印澳板块在此俯冲汇聚，致使板块消亡、板内岩浆' +
          '活动和火山地震等构造活动极为活跃，也是全球生物多样性的研究热点区域。本专项将从地球系统科学视角出发，以物质能' +
          '量汇聚中心的形成和演变机制为核心，以其能量基础和物质基础为研究主线，围绕“海气汇聚的耦合动力过程及其生源要素' +
          '输运作用”、“板块俯冲的起始机制与过程及其对暖池起源演化的影响”、“海洋生物多样性中心形成与演变的多圈层机制' +
          '” 三个关键科学问题，在印太交汇区开展“跨圈层、跨领域、跨学科”综合研究，阐释印太交汇区物质能量跨圈层交换过程' +
          '机理，创建印太交汇区物质能量中心形成机制的理论框架，取得具有国际影响力的原创性成果，推动重大理论和模式创新，' +
          '占领国际学术制高点。',
      }
    },
    methods: {
      setSize() {
        let bili = 3840 / 1460;
        this.imgheight = document.body.clientWidth / bili; //这个比例是1920/800
      },
      menu_change() {
        /*滚轮事件只有firefox比较特殊，使用DOMMouseScroll; 其他浏览器使用mousewheel;*/
        // firefox
        var direction;
        let menu = document.getElementsByClassName('header_bottom')[0];
        document.body.addEventListener("DOMMouseScroll", function (event) {
          direction = event.detail && (event.detail > 0 ? "mousedown" : "mouseup");
          menu.style.top = '-100px';
          if (event.detail < 0) {
            menu.style.position = 'fixed';
            menu.style.top = '0';
          } else {

          }
        });

        // chrome and ie
        document.body.onmousewheel = function (event) {
          event = event || window.event;
          direction = event.wheelDelta && (event.wheelDelta > 0 ? "mouseup" : "mousedown");
          menu.style.top = '-100px';
          if (event.wheelDelta > 0) {
            menu.style.position = 'fixed';
            menu.style.top = '0';
          }
        };
      }
    },
    created() {
    },
    mounted() {
      window.addEventListener("resize", () => {
        this.setSize();
      }, false);

      this.menu_change();
    }
  }
</script>

